.search-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}
h2 {
  margin-bottom: 1rem;
}

input {
  padding: 0.5rem;
}

.search-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  max-width: 500%;
}

.search-result {
 max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.search-result img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  margin-right: 1rem;
}

.search-result-text {
  text-align: center;
  margin-top: 1rem;
}